.active{
    background-color: rgb(44, 44, 44);
    color:white;
    border-radius: 4px;
    transition: all .4s ease-in;
     
}


.btn{
    scale: 0;
    opacity: 0;
    transition: all 1s ease-in-out;
}

.hide{
    scale: 0;
    opacity: 0;
    

}

.show{
    scale: 1;
    

    opacity: 1;
}